<template>
  <div class="Home">
      <div class="Homeheader">手机商城</div>
      <div class="swipe">
        <mt-swipe :auto="4000">
          <mt-swipe-item><router-link to="goodDetail?id=4"><img src="/static/img/banner1.jpg" alt="轮播图"></router-link></mt-swipe-item>
          <mt-swipe-item><router-link to="choose"><img src="/static/img/banner2.jpg" alt="轮播图"></router-link></mt-swipe-item>
        </mt-swipe>
      </div>
      <div class="HomeList">
        <ul >
          <li><div @click="xuangou"><img src="/static/img/1.png" alt="商品分类"></div></li>
          <li><div  @click="btn"><img src="/static/img/2.png" alt="品牌日"></div></li>
          <li><div @click="toOrder"><img src="/static/img/3.png" alt="我的订单"></div></li>
          <li><div @click="btn"><img src="/static/img/4.png" alt="在线客服"></div></li>
        </ul>
      </div>
      <div class="main">
        <span >热门商品</span>
        <div class="main_box">
          <ul><!-- <img src="../assets/list1.jpg" alt="商品"> -->
            <li v-for="(todo,index) in todos" :id="todo.id"  @click="open(todo.id)" class="list">
                <div class="image">
                  <img v-bind:src="todo.img" alt="图片">
                </div>
                <div class="name">{{todo.name}}</div>
                <div class="nametwo">{{todo.nametwo}}</div>
                <div class="Price">￥{{todo.Price}}</div>
            </li>
          </ul>
        </div>
      </div>
  </div>
</template>

<script>
import { mapState,mapMutations,mapGetters  } from 'vuex';
import { Swipe, SwipeItem } from 'mint-ui';
import { MessageBox } from 'mint-ui';
export default {
  name:"home",
  data(){
    return{
      todos:[]
    }
  },
  mounted:function(){
    this.getData();
  },
  methods:{
    getData:function(){
      var _this=this;
      this.$http.get("/static/home.json").then(function(res) {
        for (var i = 0, len = res.body.todos.length; i < len; i++) {
          var selData = res.body.todos[i];
          var part = res.body.todos[i].name;
          _this.todos.push(selData);
        }
      })
    },
    open:function(id){
      this.$router.push({path:"goodDetail",query:{id:id}})
    },
    xuangou:function(){
      this.$router.push({path:"choose"})
    },
    toOrder:function(){
      this.$router.push({path:"order"})
    },
    btn:function(){
      MessageBox('提示', '未展示');
    }
  },
}
</script>
<style>
  .Home{
    border-bottom: 10px;
    background: white;
  }
   .Homeheader{ 
     width: 100%;
     z-index: 1;
     height: 1.3rem;
     line-height: 1.3rem;
     font-size: 0.65rem;
     text-align: center;
   }
   .swipe{
     height: 6.5rem;
     margin-top: 1px;
   }
   .swipe img{
      width: 100%;
      height: 6.5rem;
  }
  .main_box{
    /* margin-bottom: 34px;  */
    height: 339px;

  }
  .main span{
    display: block;
    height: 1.3rem;
    background: white;
    font-size: 0.35rem;
    line-height: 1.3rem;
    padding-left: 0.3rem;
  }
  .list {
    height: 7.3rem;
    background: white;
    float: left;
    box-sizing:border-box;
    width:50%;
    border-right: 1px solid #F4F4F4;
    border-top: 1px solid #cccccc;
  }
  .list span{
    display: block;
    color: red;
    padding-left: 0.5rem;
    padding-top: 0.1rem;
  }
  .main_box ul{
      overflow: hidden;
      margin-bottom: 1.5rem;
  }
  ul li{
    list-style: none;
  }
  .name{
    width: 80%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin: auto;
    font-size: 0.37rem;
    padding-top: 0.8rem;
    font-weight: 800;
    text-align: center;
  }
    .nametwo{
      font-size: 0.01rem;
      text-align: center;
      padding-top: 0.15rem;
      font-family: "微软雅黑"
    }
    .Price{
      font-size: 0.33rem;
      color: red;
      margin: auto;
      /* padding: 0.1rem 0.9rem; */
      text-align: center;
      padding-top: 0.48rem;
      /* font-weight: bold; */
      font-size: 0.38rem;

    }
    .image{
      width: 100%;
      height: 4rem;
      background: white;
    }
    .image img{
      height: 3.3rem;
      width: 3.5rem;
      display: block;
      margin: auto;
      padding-top: 0.9rem;
    }
    .HomeList{
      width: 100%;
      height: 2.5rem;
      background: white;
    }
   
    
    .HomeList>ul>li img{
      display: block;
      width: 48px;
      height: 68px;
      margin: 15px auto;
    }
    .HomeList>ul>li{
      width: 24%;
      float: left;
      margin: auto;
    }
</style>




